/**
 *	name : vue 
 *   author : 
 *   date : 2017/6/20
 *   dependence :
 *       cropper 
 *       exif    ios
 * */


import Cropper from 'cropperjs'
import xhr from 'core-image-xhr';
import Exif from 'exif-js'
const uploadImg = require('~/assets/images/loading.gif');

let paramsData;
export default {
	install( Vue ){
		//
		Vue.prototype.initilize = function( opt ){
			// debugger;
			let self = this;

			// 
			!opt.hintTxt ? opt.hintTxt = '...' : null; 

			this.options = opt;
			
			//dom
			this.createElement();
			this.resultObj = opt.resultObj;
			//
			this.cropper = new Cropper( this.preview , {
				aspectRatio : opt.aspectRatio || 1 , // 
				autoCropArea : 1, // opt.autoCropArea || 0.8 ,
				viewMode : 1,
				guides : opt.aspectRatio == 'Free' ? false : true ,
				cropBoxResizable : opt.aspectRatio == 'Free' ? false : true ,
				cropBoxMovable : opt.aspectRatio == 'Free' ? false : true ,
				dragCrop : opt.aspectRatio == 'Free' ? false : true ,
				background : false,
				checkOrientation : true ,
				checkCrossOrigin : true ,
				zoomable : true, 
				zoomOnWheel : false ,
				center : false ,
				toggleDragModeOnDblclick : false ,
				ready : function () {
					// console.log(self.cropper.rotate(90))
					console.log('ready');

					// ios 
					if (self.Orientation) {
						switch (self.Orientation) {
							case 6:
								self.cropper.rotate(90);
								break;
							case 8:
								self.cropper.rotate(-90);
								break;
							case 3:
								self.cropper.rotate(-180);
								break
						}
						// 
						self.Orientation = null;
					}

					// self.cropper.rotate(90);
					if( opt.aspectRatio == 'Free' ){
						let cropBox = self.cropper.cropBox;
						cropBox.querySelector('span.cropper-view-box').style.outline = 'none';
						self.cropper.disable();
					}
				}
			});
		};
		//DOM
		Vue.prototype.createElement = function () {
			//
			this.preview = null;

			let str = '<div><img id="clip_image" src="originUrl"/></div><button type="button" id="cancel_clip"></button><button type="button" id="clip_button"></button>';
			str+= '<div class="crop_loading"><div class="crop_content"><img src="' + uploadImg + '"/><div class="crop_text">' + this.options.hintTxt +'</div></div></div>';
			str+= '<div class="crop_success"><div class="crop_success_text"></div></div></div>';

			let body = document.getElementsByTagName('body')[0];
			this.reagion = document.createElement('div');
			this.reagion.id = 'clip_container';
			this.reagion.className = 'container';
			this.reagion.innerHTML = str;
			//DOM
			body.appendChild(this.reagion);
			this.preview = document.getElementById('clip_image');
			
			//
			this.initFunction();
		}
		//
		Vue.prototype.initFunction = function  () {
			let self =this;
			this.clickBtn = document.getElementById('clip_button');
			this.cancelBtn = document.getElementById('cancel_clip');
			//
			this.addEvent( this.clickBtn , 'click' , function () {
				self.crop();
			})
			//
			this.addEvent( this.cancelBtn , 'click' , function () {
				self.destoried();
			})
			//input
			this.addEvent( this.fileObj , 'click' , function () {
				this.value = '';
			})
		}
		
		//input['file']change
		/**
		 * 
		 * @param {object} e input event
		 * @param {object} opt 
		 * @param {HTMLElement} opt.resultObj img
		 * @param {object} opt.headers http
		 * @param {number} opt.aspectRatio 
		 * @param {boolaen}	opt.disableOrientation 
		 * @param {string} opt.uploadServer 
		 * @param {string} opt.filed 
		 * @param {string} opt.hintTxt   ...
		 * @param {function} opt.uploadSuccess 
		 * @param {function} opt.uploadError 
		 */
		Vue.prototype.clip = function ( e , opt ) {
			let self = this;	
			
			// 
			paramsData = {
				type: null,
				filename: '',
				filed: 'file',
				base64Code: null
			};

			opt.filed ? paramsData.filed = opt.filed : null;

			this.fileObj = e.srcElement;
			
			let files = e.target.files || e.dataTransfer.files;
		
			// 
			if (!files.length) return false;
			
			// 
			if (!opt.disableOrientation) {
				Exif.getData( files[0] , function(){
					self.Orientation = Exif.getTag( files[0], 'Orientation');
					console.log(self.Orientation);
				});
			}
			
			// 
			this.uploadServer = opt.uploadServer;
			this.uploadSuccess = opt.uploadSuccess;
			this.uploadError = opt.uploadError;
			this.headers = opt.headers;

			//
			this.initilize( opt );
			//
			this.picValue = files[0];
			//url
			this.originUrl = this.getObjectURL( this.picValue );

			// 
			if (opt.aspectRatio !== undefined) {
				this.cropper.setAspectRatio(opt.aspectRatio);
			}

			//url
			if(this.cropper){
				this.cropper.replace(this.originUrl);
			}
		}
		// url
		Vue.prototype.getObjectURL = function(file) {
			let url = null ;
			if (window.createObjectURL!== undefined) { // basic
				url = window.createObjectURL(file) ;
			} else if (window.URL!== undefined) { // mozilla(firefox)
				url = window.URL.createObjectURL(file) ;
			} else if (window.webkitURL!== undefined) { // webkit or chrome
				url = window.webkitURL.createObjectURL(file) ;
			}
			return url ;
		}
		//
		Vue.prototype.crop  = function () {
			let self = this;
			let image = new Image();
			let croppedCanvas;
			let roundedCanvas;
			// Crop
			document.querySelector('.crop_loading').style.display = 'block';
			setTimeout(function () {
				croppedCanvas = self.cropper.getCroppedCanvas();
				// Round
				roundedCanvas = self.getRoundedCanvas(croppedCanvas);
				let imgData = roundedCanvas.toDataURL();
				
				//100k,
				if( imgData.length < ( 100 * 1024) ){ 
					// 10 * 1024 100k
					self.resultObj.src = imgData;
					// 
					self.postImg( imgData );
				}else{
					image.onload = function () {
						//
						let data = self.compress( image , self.Orientation );
						self.resultObj.src = data;
						//
						self.postImg( data );
					}
					image.src = imgData;
				}
			},1000)
		}
		//
		Vue.prototype.getRoundedCanvas = function(sourceCanvas) {
			
			let canvas = document.createElement('canvas');
			let context = canvas.getContext('2d');
			let width = sourceCanvas.width;
			let height = sourceCanvas.height;
			
			canvas.width = width;
			canvas.height = height;
			
			context.imageSmoothingEnabled = true;
			context.drawImage(sourceCanvas, 0, 0, width, height);
			context.globalCompositeOperation = 'destination-in';
			context.beginPath();
			context.rect(0 , 0 , width , height );
			context.fill();
			
			return canvas;
		}
		//
		Vue.prototype.destoried = function () {
			let self = this;
			//
			this.removeEvent( this.clickBtn , 'click' , null );
			this.removeEvent( this.cancelBtn , 'click' , null );
			this.removeEvent( this.fileObj , 'click' , null );
			//
			this.reagion.parentNode.removeChild(this.reagion);
			
			//
			this.cropper.destroy();
			this.cropper = null;
		}
		//
		Vue.prototype.postImg = function( imageData ) {
			// debugger;

			//
			console.log('');
			const url = this.uploadServer;
			let self = this;
			
			const arr = imageData.split(';base64,');
			console.log(arr.length);
			let fileType = arr[0];
			fileType = fileType.split('data:')[1];
	
			paramsData.base64Code = imageData;
			paramsData.filename = this.picValue.name;
			paramsData.type = fileType;

			const headers = this.headers || {};
			
			xhr('POST', url, headers, paramsData, function (res) {
				console.log('');
				console.log(res);
				const loading = document.querySelector('.crop_loading');
				loading ? loading.style.display = 'none' : null;
				const success = document.querySelector('.crop_success');
				success ? success.style.display = 'block' : null;
				if (res && res.msgStatus === 0) {
					self.uploadSuccess && self.uploadSuccess(res);
				} else {
					self.uploadError && self.uploadError(res);
				}
				//
				self.destoried();
			}, function (err) {
				console.log('');
				console.log(err)
				self.uploadError && self.uploadError({ msgText: '' });
				// window.uploadError();
				const loading = document.querySelector('.crop_loading');
				loading ? loading.style.display = 'none' : null;
				const success = document.querySelector('.crop_success');
				success ? success.style.display = 'block' : null;
				//
				self.destoried();
			}, true, true);
		}
		
		//
		Vue.prototype.rotateImg = function( img ,  direction , canvas ) {
			//4
			const min_step = 0;
			const max_step = 3;
			if (img == null )return;
			//imgimg
			let height = img.height;
			let width = img.width;
			let step = 2;
			if (step == null) {
				step = min_step;
			}
			if ( direction == 'right') {
				step++;
				//
				step > max_step && (step = min_step);
			} else {
				step--;
				step < min_step && (step = max_step);
			}
			//
			let degree = step * 90 * Math.PI / 180;
			let ctx = canvas.getContext('2d');
			switch (step) {
				case 0:
					canvas.width = width;
					canvas.height = height;
					ctx.drawImage(img, 0, 0);
					break;
				case 1:
					canvas.width = height;
					canvas.height = width;
					ctx.rotate(degree);
					ctx.drawImage(img, 0, -height);
					break;
				case 2:
					canvas.width = width;
					canvas.height = height;
					ctx.rotate(degree);
					ctx.drawImage(img, -width, -height);
					break;
				case 3:
					canvas.width = height;
					canvas.height = width;
					ctx.rotate(degree);
					ctx.drawImage(img, -width, 0);
					break;
			}
		}
		
		//  img element Orientation    
		Vue.prototype.compress = function ( img , Orientation ) {
			let canvas = document.createElement("canvas"); //  canvas
			let ctx = canvas.getContext('2d');
			//canvas
			let tCanvas = document.createElement("canvas");
			let tctx = tCanvas.getContext("2d");
			let initSize = img.src.length;
			let width = img.width;
			let height = img.height;

			// alert('width' + width);
			// alert('height' + height);
			// alert(initSize);
			
			//2400
			let ratio;
			if ((ratio = width * height / 9400000 ) > 1) { // 4000000
				console.log("940")
				// debugger;
				ratio = Math.sqrt(ratio);
				width /= ratio;
				height /= ratio;
			} else {
				ratio = 1;
			}
			canvas.width = width;
			canvas.height = height;
			//        
			ctx.fillStyle = "#fff";
			ctx.fillRect(0, 0, canvas.width, canvas.height);
			//100
			let count;
			if ((count = width * height / 1000000) > 1) {
				count = ~~(Math.sqrt(count) + 1); //
				//            
				let nw = ~~(width / count);
				let nh = ~~(height / count);
				tCanvas.width = nw;
				tCanvas.height = nh;
				for (let i = 0; i < count; i++) {
					for (let j = 0; j < count; j++) {
						tctx.drawImage(img, i * nw * ratio, j * nh * ratio, nw * ratio, nh * ratio, 0, 0, nw, nh);
						ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);
					}
				}
			} else {
				ctx.drawImage(img, 0, 0, width, height);
			}
			//ios 
			// if( Orientation != "" && Orientation != 1){
			// 	switch(Orientation){
			// 		case 6://90
			// 			this.rotateImg(img,'left',canvas);
			// 			break;
			// 		case 8://90
			// 			this.rotateImg(img,'right',canvas);
			// 			break;
			// 		case 3://180
			// 			this.rotateImg(img,'right',canvas);//
			// 			this.rotateImg(img,'right',canvas);
			// 			break;
			// 	}
			// }

			//
			let ndata = canvas.toDataURL( 'image/jpeg' , 0.5); //  0 ~ 1
			console.log('' + initSize);
			console.log('' + ndata.length);
			console.log('' + ~~(100 * (initSize - ndata.length) / initSize) + "%");
			tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0;
			
			return ndata; // base64 
		}
		
		//
		Vue.prototype.addEvent = function ( obj , type , fn ) {
			if( obj.addEventListener ){
				obj.addEventListener( type , fn , false );
			}else{
				obj.attachEvent( 'on' + type , fn );
			}
		}
		//
		Vue.prototype.removeEvent = function ( obj , type , fn ) {
			if( obj.removeEventListener ){
				obj.removeEventListener( type , fn , false );
			}else{
				obj.detachEvent( 'on' + type , fn );
			}
		}
	}
}



