import Vue from 'vue';
import Component from 'vue-class-component';

// import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';

import { Action, State } from 'vuex-class';
import popup from 'muse-ui/popup';
import { MODAL_0, MODAL_1, UPLOAD_AVATAR_URL, UPLOAD_ID_CARD } from '~/config';
import router from '~/router';

import 'vue-smooth-picker/dist/css/style.css';
import SmoothPicker from 'vue-smooth-picker';
import { ToastMsg } from '~/entity/toast';
import { IAuth } from '~/entity/auth';

// import '~/vendors/photoView/image-collector.js';
// import '~/assets/css/image-collector.less';

import clipper from '~/vendors/clipper/clipper.js'; // 
import '~/vendors/clipper/style.css';

/*  */
import xhr from 'core-image-xhr';
import { IImgXhrParams } from '~/entity/imgxhr';

// import '~/vendors/photoView/image-collector.js';
Vue.use(SmoothPicker);
Vue.use(clipper);

let count = 0;

// 
import Croppa from '~/components/croppa/index.vue';
import { ICroppaOption } from '~/components/croppa/croppa';

// 
// import VuePreview from 'vue-preview';
// Vue.use(VuePreview);

// // # 
// import preview from 'vue-photo-preview';
// import 'vue-photo-preview/dist/skin.css';
// Vue.use(preview);

const img1 = require('../../assets/images/avatar.png');
const img2 = require('../../assets/images/weixiaobao.png');
const img3 = require('../../assets/images/timg.jpg');

@Component({
    components: {
        'mu-popup': popup,
        'my-croppa': Croppa
    }
})
export default class TestComp extends Vue {

    $refs: {
        mychild: any
    };

    @State(state => state.user.user)
    userInfo;

    /* modal */
    @State(state => state.modal.MODAL_0)
    isShowModal;    

    @State(state => state.modal.MODAL_1)
    isShowModal2;

    @Action('addModal')
    addModal;

    @Action('showLoading')
    showLoading;

    @Action('hideLoading')
    hideLoading;

    @Action('showToast')
    showToast;

    a = false;

    // picker
    pickerData: any[] = [
        {
          currentIndex: 0,
          flex: 3,
          list: [
            'Plan A - free', 'Plan B - $50', 'Plan C - $100'
          ],
          onClick: this.clickOnPlan,
          textAlign: 'center',
          className: 'row-group'
        },
        {
          divider: true,
          flex: 1,
          text: 'product',
          textAlign: 'center',
          className: 'divider'
        },
        {
          currentIndex: 2,
          flex: 3,
          list: [
            '1 * A item', '2 * A items', '3 * A items', '4 * A items', '5 * A items'
          ],
          onClick: this.clickOnProduct,
          textAlign: 'center',
          className: 'item-group'
        }
    ];

    myCroppaOption: ICroppaOption = {
        compressionRate: 0.8,
        quality: 2
        // fromCamera: true
    };

    isShowCropper = false;

    imgs = [
        {
            src: img1,
            w: 414,
            h: 736
        },
        {
            src: img2,
            w: 414,
            h: 736
        },
        {
            src: img3,
            w: 414,
            h: 736
        }
    ];

    clickob() {
        console.log('test');
        // this['$observables'].obtest.subscribe(msg => {
        //     console.log(msg);
        // });
        count++;
        this['$observables'].obtest.next(count);
    }

    change() {
        // console.log('change');
        const ciList = this.$refs['smoothPicker'].getCurrentIndexList();
        const planDetail = this.pickerData[0].list[ciList[0]];
        const productDetail = this.pickerData[2].list[ciList[2]];
        console.log(planDetail);
        console.log(productDetail);

        // if (productDetail === ) {

        // }
    }

    clickOnPlan() {
        console.log('clickOnPlan');
    }

    clickOnProduct() {
        console.log('clickOnProduct');
    }

    openModal() {
        this.addModal(MODAL_0);
    }

    closeModel() {
        history.back();
    }

    openModal2() {
        this.addModal(MODAL_1);
    }   
    
    closeMode2() {
        history.back();
    }

    enterCommentView() {
        router.push({
            name: 'commentView',
            params: {
                action: 'mySelf',
                id: this.userInfo.uid
            }
        });
    }

    activated() {
        this.a = true;
    }

    deactivated() {
        this.a = false;
    }

    uploadPhoto(event) {
        let image = document.getElementById('auth-img'); // image
        this['clip'](event , { // clip
            resultObj : image,
            headers: {
                'access-token': this['$store'].state.token
            },
            hintTxt: '',
            aspectRatio: NaN,
            disableOrientation: true,
			// aspectRatio : 'Free', // 
            uploadServer: 'http://172.18.10.242:8080', // UPLOAD_ID_CARD,
            filed: 'idcard',
            uploadSuccess(res) {
                console.log('uploadSuccess');
                console.log(res);
                this.showToast(new ToastMsg(''));
            },
            uploadError(res) {
                console.log('uploadError');   
                this.showToast(new ToastMsg(res.msgText));
            }
        });
    }

    changeImg(event) {
        this.$refs['cropper'].changeFile(event);
    }

    /**
     * url
     */
    getObjectURL(file) {
        let url = null ;
        if (window['createObjectURL'] !== undefined) { // basic
            url = window['createObjectURL'](file) ;
        } else if (window.URL !== undefined) { // mozilla(firefox)
            url = window.URL.createObjectURL(file) ;
        } else if (window['webkitURL'] !== undefined) { // webkit or chrome
            url = window['webkitURL'].createObjectURL(file) ;
        }
        return url;
    }

    cancelCrop() {
        this.isShowCropper = false;
        console.log('');
    }

    confirmCrop(data) {
        console.log('');
        const url = UPLOAD_AVATAR_URL;
        const headers = {
            'access-token': this['$store'].state.token
        };
        const params: IImgXhrParams = {
            base64Code: data.base64Code,
            type: data.type,
            filename: data.filename,
            filed: 'file'
        };
        const cropper = this.$refs['cropper'];
        this.showLoading();
        xhr('POST', url, headers, params, res => {
            console.log('');
            console.log(res);
            this.hideLoading();
            cropper.hide();
        }, err => {
            console.log('');
            console.log(err);
            this.hideLoading();
            cropper.hide();
        }, true, true);
    }

    /*  */
    chooseFile() {
        this.myCroppaOption = {
            compressionRate: 0.8,
            quality: 2,
            fullScreen: false,
            fromCamera: false
        };
        this.$refs['croppa'].chooseFile();
    }

    choosePhoto() {
        this.myCroppaOption = {
            compressionRate: 0.5,
            quality: 6,
            fullScreen: true,
            fromCamera: false
        };
        this.$refs['croppa'].chooseFile();
    }

    myCroppaConfirm(data) { 
        // debugger;
        const url = UPLOAD_ID_CARD;
        const headers = {
            'access-token': this['$store'].state.token
        };
        const params: IImgXhrParams = {
            base64Code: data.base64Code,
            type: data.type,
            filename: data.filename,
            filed: 'idcard'
        };
        const croppa = <any>this.$refs['croppa'];
        this.showLoading();
        xhr('POST', url, headers, params, res => {
            console.log('');
            console.log(res);
            if (res.msgStatus === 0) {
                alert(res.obj.name);
                this.showToast(new ToastMsg(''));
            } else {
                this.showToast(new ToastMsg(res.msgText));
            }
            
            this.hideLoading();
            croppa.cancel();
        }, err => {
            console.log('');
            console.log(err);
            this.hideLoading();
            this.showToast(new ToastMsg(''));
            croppa.cancel();
        }, true, true);
    }

    /*  */
    showImg(index: number) {
        this['$preview'].open(index, this.imgs);
    }
}



